.overlay {
  background-color: rgba(0, 0, 0, 0.7);
  bottom: 0;
  cursor: default;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  top: 0;
  visibility: hidden;
  z-index: 99999;
  transition: opacity .5s;
}

.popup {
  background-color: #fff;
  border: 3px solid #fff;
  left: 50%;
  opacity: 0;
  padding: 15px;
  width: 90vw;
  height: 66vh;
  min-height: 510px;
  position: fixed;
  text-align: justify;
  top: 53vh;
  visibility: hidden;
  z-index: 999999;
  transform: translate(-50%, -50%);
  transition: opacity .5s, top .5s;
  border-radius: 11px;
}

.popup .close_window {
  font-size: 13px;
  display: block;
  width: 6px;
  height: 17px;
  position: absolute;
  padding: 1px 9px 4px 9px;
  top: -15px;
  right: -15px;
  cursor: pointer;
  color: #fff;
  background: #002d40;
  border-radius: 50%;
  text-align: center;
  box-shadow: -1px 1px 3px rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
}

.popup .close_window img {
  width: 100%;
  margin-top: 3px;
}

.popup .close_window:hover, .swiper-pagination-bullet-active {
  background: #002d40;
}

.popup .swiper-container {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.popup .swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  height: 300px;
}

.popup .swiper-container {
  background-color: transparent;
}

